/* General Page Styling */
.about-page {
  padding: 40px;
  max-width: 800px;
  margin: auto;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #2c3e50; /* Professional dark gray-blue */
}

/* Title Styling */
.about-content h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #ffffff;
  text-align: center;
  background: linear-gradient(90deg, #0073e6, #005bb5); /* Clean professional blue gradient */
  padding: 15px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  font-weight: bold;
}

/* About Me Card */
.about-me-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  margin-bottom: 40px;
  background: linear-gradient(135deg, #ffffff, #e8f3fc); /* Subtle light gradient */
  border-radius: 20px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
  color: #2c3e50;
  text-align: center;
  position: relative;
  overflow: hidden;
}

/* Glowing Border Animation */
.about-me-card::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(0, 115, 230, 0.2), transparent);
  animation: glow-animation 3s infinite alternate ease-in-out;
  transform: rotate(45deg);
  border-radius: 50%;
  z-index: -1;
}

@keyframes glow-animation {
  0% {
    opacity: 0.5;
    transform: scale(1);
  }
  100% {
    opacity: 1;
    transform: scale(1.1);
  }
}

/* Profile Picture Styling */
.profile-picture {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin-bottom: 15px;
  object-fit: cover;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  border: 4px solid #0073e6;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.profile-picture:hover {
  transform: scale(1.1) rotate(5deg);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5);
}

/* Name and Details */
.about-me-details .name {
  font-weight: bold;
  font-size: 1.6rem;
  color: #005bb5;
  margin-bottom: 10px;
}

.about-me-details p {
  margin: 5px 0;
  color: #4a4a4a;
  font-size: 1.1rem;
}

/* Subtle Hover Effect on Card */
.about-me-card:hover {
  box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.2);
  transform: translateY(-5px);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Contact Information Section */
.contact-info {
  background: #f5f7fa; /* Light gray for a clean look */
  padding: 20px;
  border-radius: 15px;
  margin-top: 20px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  color: #2c3e50;
}

.contact-info ul {
  list-style-type: none;
  padding: 0;
}

.contact-info li {
  display: flex;
  align-items: center;
  background-color: rgba(0, 115, 230, 0.1);
  margin-bottom: 10px;
  padding: 10px 15px;
  border-radius: 8px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

.contact-info li:hover {
  background-color: rgba(0, 115, 230, 0.2);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
}

.contact-info a {
  color: #0073e6;
  text-decoration: none;
  margin-left: 10px;
  font-weight: bold;
}

.contact-info a:hover {
  text-decoration: underline;
}

/* Personal Note Styling */
.personal-message {
  background-color: #f5f7fa;
  margin-bottom: 10px;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  color: #2c3e50;
}

.personal-message-title {
  color: #005bb5;
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 15px;
}

/* Paragraph Styling */
.white-paragraph {
  color: #2c3e50; /* Matches main text color for consistency */
  line-height: 1.6;
}

.about-content h2,
.contact-info h2 {
  color: #005bb5;
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 15px;
}
