:root {
  --color-header-bg: #255790; /* Dark Blue-Gray for the header */
  --color-section-bg-1: #f5f7fa; /* Light Gray for alternating sections */
  --color-section-bg-2: #ffffff; /* White for alternating sections */
  --color-text: #2c3e50; /* Dark Gray-Blue for primary text */
  --color-heading: #1a1a1a; /* Black for headings */
  --color-heading-light: #5a5a5a; /* Light Gray for subheadings */
  --color-link: #0073e6; /* Professional Blue for links */
  --color-tint: rgba(0, 115, 230, 0.1); /* Light blue tint for hover effects */
  --header-fg: #ffffff; /* White foreground for header text */
}

/* General Styles */
body {
  background-color: var(--color-section-bg-1);
  color: var(--color-text);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Header Styles */
.App-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  background-color: var(--color-header-bg);
  color: var(--header-fg);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.header-title {
  font-size: 2rem;
  font-weight: bold;
  color: var(--header-fg);
  margin: 0;
}

.header-middle {
  display: flex;
  gap: 20px;
}

.button {
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: bold;
  text-decoration: none;
  color: var(--header-fg);
  background: linear-gradient(90deg, #0056b3, #003f8a); /* Gradient blue */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.button:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.nav-menu {
  display: flex;
  gap: 20px;
}

/* Hero Section */
.hero-section {
  background-color: var(--color-section-bg-2);
  padding: 100px 20px;
  text-align: center;
  color: var(--color-text);
}

.hero-content {
  max-width: 800px;
  margin: 0 auto;
}

.hero-title {
  font-size: 3em;
  margin-bottom: 20px;
  color: var(--color-heading);
}

.hero-description {
  font-size: 1.25em;
  color: var(--color-heading-light);
  line-height: 1.6;
}

.hero-buttons {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-top: 20px;
}

.cta-button {
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #ffffff;
  background-color: var(--color-link);
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.cta-button:hover {
  background-color: #005bb5;
  transform: translateY(-3px);
}

/* Section Background Alternating Colors */
.section {
  padding: 60px 20px;
  text-align: center;
  transition: background-color 0.3s ease;
}

.section:nth-of-type(even) {
  background-color: var(--color-section-bg-1);
}

.section:nth-of-type(odd) {
  background-color: var(--color-section-bg-2);
}

.section h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: var(--color-heading);
}

.section p,
.section .cta-button.contact {
  color: var(--color-text);
}

.card-container {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

/* Footer Section */
.conclusion {
  padding: 60px 20px;
  background-color: var(--color-header-bg);
  color: var(--header-fg);
}

.conclusion .cta-button.contact {
  background-color: var(--color-link);
  color: #ffffff;
}

.conclusion .cta-button.contact:hover {
  background-color: #005bb5;
}

/* Responsive Hero Title */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2rem;
  }

  .hero-description {
    font-size: 1rem;
  }
}

/* Testimonials Section */
.testimonials {
  padding: 60px 20px;
  background-color: var(--color-section-bg-1);
  color: var(--color-text);
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
}

.section-title {
  font-size: 2rem;
  color: var(--color-link);
  margin-bottom: 30px;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}

.testimonials-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.testimonial-item {
  font-size: 1rem;
  color: var(--color-text);
  background-color: var(--color-section-bg-2);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  text-align: left;
  max-width: 700px;
  margin: 0 auto;
}

.testimonial-item h3 {
  font-size: 1.2rem;
  color: var(--color-heading);
  margin-bottom: 10px;
}

.testimonial-item p {
  font-style: italic;
  color: var(--color-heading-light);
}

/* Responsive Design Adjustments */
@media (max-width: 768px) {
  .section-title {
    font-size: 1.5rem;
  }

  .testimonial-item {
    font-size: 1rem;
  }

  .testimonial-item h3 {
    font-size: 1.1rem;
  }
}

/* Badge and Certificate Styling */
.badge-container {
  display: flex;
  flex-direction: column; /* Arrange items vertically */
  align-items: center; /* Center all items horizontally */
  gap: 20px; /* Add spacing between each badge-text pair */
  margin: 20px 0;
}

.badge-item {
  display: flex; /* Arrange badge and text in a row */
  align-items: center; /* Align badge and text vertically */
  gap: 10px; /* Space between badge and text */
  justify-content: center; /* Center the row content */
  text-align: center; /* Center text below each badge */
}

.badge-image {
  width: 50px; /* Smaller size for badges */
  height: auto;
  flex-shrink: 0; /* Prevent badges from resizing */
  border-radius: 5px; /* Add slight rounding for consistency */
}

.certificate-image {
  width: 100%; /* Full width of the container */
  max-width: 300px; /* Limit maximum size */
  height: auto;
  margin-top: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  display: block; /* Ensure proper block-level alignment */
}

.portfolio-summary {
  font-size: 1rem; /* Adjust font size for compact look */
  line-height: 1.6;
  color: var(--color-heading-light);
  text-align: center; /* Align text to the center */
  margin: 10px 0; /* Smaller margin for tighter layout */
}

.svg-content {
  display: flex;
  flex-direction: column;
  gap: 20px; /* Space between items */
  margin-top: 20px;
}

.svg-item {
  display: flex;
  align-items: center;
  gap: 15px; /* Space between SVG and text */
}

.icon {
  width: 50px; /* Consistent size for all icons */
  height: auto;
  flex-shrink: 0; /* Prevent icons from resizing */
}

.lamp {
  display: flex; /* Enable Flexbox */
  align-items: center; /* Align items vertically */
  justify-content: center; /* Align items horizontally (optional) */
  gap: 20px; /* Add spacing between elements (optional) */
}
