/* Main Portfolio Container */
.cybersecurity-portfolio {
  padding: 40px;
  max-width: 800px;
  margin: auto;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: var(--color-section-bg-2);
  color: var(--color-text);
  border-radius: 12px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}

.cybersecurity-portfolio:hover {
  transform: scale(1.02);
}

/* Portfolio Header */
.portfolio-header {
  text-align: center;
  margin-bottom: 30px;
}

.portfolio-header h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #ffeb3b;
  text-align: center;
  background: linear-gradient(90deg, #4a90e2, #357abd);
  padding: 15px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  font-weight: bold;
}

.portfolio-header p {
  font-size: 1.2rem;
  color: #ffffff !important; /* Forces project descriptions to be white */
  line-height: 1.6;
  max-width: 700px;
  margin: 0 auto;
}

/* Project List */
.project-list {
  list-style: none;
  padding: 0;
  margin-top: 20px;
}

.project-item {
  background: radial-gradient(circle, #0567fa, #0044cc);
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 12px;
  color: #ffffff;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-item:hover {
  transform: translateY(-5px);
  box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.5);
}

/* Glowing Effect */
.project-item::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.2), transparent);
  animation: glow-animation 3s infinite alternate ease-in-out;
  transform: rotate(45deg);
  border-radius: 50%;
  z-index: -1;
}

@keyframes glow-animation {
  0% {
    opacity: 0.5;
    transform: scale(1);
  }
  100% {
    opacity: 1;
    transform: scale(1.1);
  }
}

/* Project Button */
.project-button {
  font-size: 1.3rem;
  font-weight: 600;
  color: #ffeb3b;
  background: none;
  border: none;
  cursor: pointer;
  text-align: left;
  width: 100%;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
  transition: color 0.3s ease;
}

.project-button:hover {
  color: #ffffff;
}

/* Project Description */
.project-description {
  font-size: 1rem;
  color: #ffffff !important; /* Forces project descriptions to be white */
  margin-top: 8px;
}

/* Modal Overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Modal Content */
.modal-content {
  background-color: #0044cc;
  padding: 30px;
  border-radius: 12px;
  max-width: 700px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  color: #ffffff;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.modal-header h2 {
  font-size: 1.8rem;
  color: #ffeb3b;
}

.close-button {
  font-size: 1.5rem;
  background: none;
  border: none;
  color: #ffeb3b;
  cursor: pointer;
  transition: color 0.3s ease;
}

.close-button:hover {
  color: #ffffff;
}

/* Modal Body */
.modal-body {
  font-size: 1rem;
  color: #e0f7fa;
}

.techniques, .tools {
  margin-top: 20px;
}

.techniques h3, .tools h3 {
  font-size: 1.1rem;
  color: #ffeb3b;
  margin-bottom: 10px;
  font-weight: bold;
}

.techniques p, .tools ul {
  margin-left: 20px;
  font-size: 1rem;
  color: #e0f7fa;
}

.tools ul {
  list-style-type: disc;
  padding-left: 20px;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .portfolio-header h1 {
    font-size: 2rem;
  }

  .portfolio-header p {
    font-size: 1rem;
  }
}
