/* src/pages/Modal.css */

.modal-overlay {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  width: 100vw !important;
  height: 100vh !important;
  background: rgba(0, 0, 0, 0.8) !important; /* Adjust opacity as needed */
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  z-index: 10000 !important; /* Ensure it's on top */
  overflow: hidden !important;
}

.modal-content {
  background-color: #ffffff !important;
  border-radius: 8px !important;
  padding: 20px !important;
  width: 90% !important;
  max-width: 600px !important; /* Adjust to fit more content */
  max-height: 90vh !important; /* Adds limit to prevent overflow */
  overflow-y: auto !important; /* Allows scrolling within modal content if necessary */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2) !important;
  animation: fadeIn 0.3s ease-in-out !important;
  font-family: 'Arial', sans-serif !important;
  position: relative !important;
  z-index: 10001 !important; /* Ensure content appears above overlay */
}

.close-button {
  position: absolute !important;
  top: 12px !important;
  right: 12px !important;
  background: none !important;
  border: none !important;
  font-size: 24px !important;
  color: #333 !important;
  cursor: pointer !important;
  transition: color 0.2s ease !important;
}

.close-button:hover {
  color: #ff5c5c !important;
}

@keyframes fadeIn {
  from { opacity: 0; transform: scale(0.9); }
  to { opacity: 1; transform: scale(1); }
}
